html {
  font-family: 'Indie Flower';
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

h1, h2 {
  font-family: 'Libre Baskerville'; }

body {
  background-image: linear-gradient(to bottom, #227713, #419317, #61b018, #83ce17, #a8eb12); }

.grid {
  display: grid;
  grid-template-areas: "header header header" "content . menu"  "footer footer footer";
  grid-template-columns: [pierwsza] 1fr [druga] 50px [trzecia] 200px [czwarta];
  grid-template-rows: 100px auto 100px;
  grid-column-gap: 16px;
  grid-row-gap: 16px; }
  .grid__item {
    grid-row-start: 1;
    grid-row-end: 3;
    grid-column-start: pierwsza;
    grid-column-end: span 2;
    z-index: -1; }

.header {
  grid-area: header;
  text-align: center; }
  .header__title {
    margin-bottom: 10px; }
    .header__title--red {
      color: darkred; }

.article {
  grid-area: content;
  display: flex;
  border: solid 1px black;
  flex-flow: column wrap;
  padding: 2px; }

.flex__arange {
  border: solid 1px red;
  margin: 2px;
  overflow: hidden;
  text-align: center; }

.navigation {
  grid-area: menu; }
  .navigation__list {
    flex-direction: row;
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: center; }
  .navigation__item {
    margin: 0 16px; }

.footer {
  grid-area: footer;
  text-align: center; }

.image {
  float: left;
  width: 100px;
  height: 178px;
  margin-left: 10px;
  margin-bottom: 10px;
  margin-right: 3px;
  border-radius: 1em 4em 1em 4em / 2em 1em 2em 1em;
  border: 5px dotted orange; }

.akapit {
  text-align: justify;
  margin-inline-start: 10px;
  margin-inline-end: 10px;
  text-indent: 20px;
  /*wcięcie*/ }
  .akapit:first-letter {
    font-size: 200%; }

.naglowek {
  padding: 1px;
  margin-bottom: 0px;
  margin-left: auto;
  margin-right: auto;
  font-style: italic;
  text-decoration-line: underline;
  text-shadow: 1px 1px 3px #a8eb12; }
  .naglowek__przed::before {
    content: "  ";
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-right: 5px;
    background: yellow; }
  .naglowek__przed::after {
    content: "  ";
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-left: 5px;
    background: red;
    transition: transform 2s ease-in-out; }
  .naglowek__przed:hover::after {
    transform: translateX(50px); }

@media (min-width: 661px) {
  .main {
    max-width: 900px; }
  .navigation__switcher {
    display: none; } }

@media (max-width: 660px) {
  .navigation__switcher {
    margin-top: 10px; }
  .navigation__list {
    flex-direction: column;
    display: none; }
  .navigation__list--visible {
    display: flex;
    background-image: linear-gradient(to bottom, #227713, #419317, #61b018, #83ce17, #a8eb12); }
  .navigation__item {
    margin: 16px; }
  .navigation__switcher {
    border: none;
    background: none; }
  .grid {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 50px 100px auto 100px;
    grid-template-areas: "menu" "header" "content" "footer";
    grid-column-gap: 0px; } }

.head {
  font-weight: 600;
  font-size: 44px;
  margin: 0;
  margin-bottom: 48px; }

.form__actions {
  margin-top: 48px;
  display: flex;
  justify-content: space-around;
  text-align: center; }

.form__button {
  border: 1px solid #463c3c;
  border-radius: 8px;
  padding: 16px; }
  .form__button--load {
    background: white;
    color: black;
    transition: background .3s linear, color .5s ease-in-out; }
    .form__button--load:hover {
      background: green;
      color: pink; }
  .form__button--save {
    background: black;
    color: white; }

.form__label {
  display: block;
  font-size: 14px; }

.form__message {
  width: 60%;
  border-radius: 8px;
  border: 1px solid #463c3c;
  min-height: 30vh;
  padding: 12px; }

.anim {
  width: 50px;
  height: 50px;
  background: red;
  border: 1px black solid;
  transition: transform 1s ease-in-out; }
  .anim:hover {
    transform: translateY(-20px) scale(2);
    bottom: 20px; }
